<template>
    <div id="ArticleList">
        <mt-table title="文章列表">
            <template slot="table">
                <table>
                    <tr>
                        <th>标题</th>
                        <th>日期</th>
                        <th>作者</th>
                        <th>文章内容</th>
                        <th>文章类型</th>
                        <th>是否已发布</th>
                        <th>操作</th>
                    </tr>
                    <tr v-for="(item,index) in allArticles.data" :key="index">
                        <td>{{item.title}}</td>
                        <td>{{item.pubDate | formateDate}}</td>
                        <td>{{item.authore}}</td>
                        <td>{{item.content.slice(0,10)+'...'}}</td>
                        <td>{{item.category}}</td>
                        <td>{{item.isPublished?'已发布':'未发布'}}</td>
                        <td>
                            <span style="color:red" @click="deleteArticle(item)">删除</span>
                                &nbsp;
                            <span style="color:#ccc">|</span>&nbsp;
                            <span style="color:#409EFF" @click="editArticle(item)">编辑</span>
                             &nbsp;
                            <span style="color:#ccc">|</span>&nbsp;
                            <span style="color:orange" @click="preview(item)">预览</span>
                        </td>
                    </tr>
                </table>
            </template>
            <template slot="pagination">
                <el-pagination
                background
                layout="prev, pager, next"
                :total="allArticles.total"
                @current-change="loadMore('loadAllArticles',$event)"
                :page-size="10"
                >
                </el-pagination>
            </template>
        </mt-table>
    </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
    data(){
        return {
            
        }
    },
    computed: {
        ...mapGetters(['allArticles'])
    },
    methods:{
        preview(item){
            this.$router.push({name:'previewArticle',params:{content:item.content}})
        },
        deleteArticle(item){
            var action = this.post(this.$apis.deleteArticle,{_id:item._id});
            this.operatorConfirm("删除该文章么?",action).then(()=>{
                this.$store.dispatch('loadAllArticles')
            });
        },
        editArticle(item){
            this.$router.push({name:'addNewArticle',params:{article:item,isUpdate:true}})
        }
    },
    created() {
        // console.log(this.$apis.findArticle);
        this.$store.dispatch('loadAllArticles')
    },
}
</script>

<style lang="scss" scoped>
    #ArticleList{
        width: 100%;
        margin-top: 10px;
    }
</style>
